.noBorder {
  border: 0 !important;
}

.showLogo {
  display: flex;
  transition: 0.2s all;
  opacity: 1;
  transform: translateX(0);
}

.hideLogo {
  composes: showLogo;
  opacity: 0;
  transform: translateX(-6rem);
}

/* SMALL */
@media screen and (max-width: 768px) {
  .customHeader {
    padding: 0 var(--spacing-small);
  }
}
